import React, { useRef, useState } from "react"; //hook  x l cc state cc b
import { useEffect } from "react"; // hook thc hin cc side effect trong component
import { useData } from "../../../custom/GlobalData"; // Import GlobalData t useContext
import InformationMove from "./InformationMove"; // Import component InformationMove  hin th thng tin phim
import { useDispatch, useSelector } from "react-redux"; // 'useDispatch' cho php bn dispatch actions ti Redux store. 'useSelector' cho php bn trch xut data t Redux store.
// 'fetchTrailerKey' l mt async thunk dng  ly trailer key t API.
// 'resetState' l mt action dng  reset state v gi tr ban u.
import { fetchTrailerKey, resetState } from "../../../reducer/movieSlice";
import { MdChevronLeft, MdChevronRight } from "react-icons/md";
const MovieList = () => {
  // S dng useContext  ly d liu phim t Global State.
  const { movies } = useData();

  // Lu 10 phim ngu nhin ca Trending vo mng
  const [randomMovies, setRandomMovies] = useState([]);
  // S dng useDispatch  c th dispatch cc actions n Redux store.
  const dispatch = useDispatch();
  // S dng useSelector  ly 'Trailerkey' v 'status' t state ca Redux store.
  const Trailerkey = useSelector((state) => state.movies.key);
  const status = useSelector((state) => state.movies.status);
  // Khi to state cc b  theo di trng thi hin th ca cc danh sch phim.
  const [movieStates, setMovieStates] = useState({
    // Mi danh sch phim c mt object vi cc thuc tnh: mounted, data, v key.
    fetchTrending: { mounted: false, data: null, key: null },
    randomTrendingMovies: { mounted: false, data: null, key: null },
    fetchTopRated: { mounted: false, data: null, key: null },
    fetchActionMovies: { mounted: true, data: null, key: null },
    fetchComedyMovies: { mounted: false, data: null, key: null },
    fetchHorrorMovies: { mounted: false, data: null, key: null },
    fetchRomanceMovies: { mounted: false, data: null, key: null },
    fetchDocumentaries: { mounted: false, data: null, key: null },
  });

  // Hm  trn v ly 10 phim ngu nhin
  const getRandomMovies = (moviesArray, count) => {
    const shuffled = [...moviesArray].sort(() => 0.5 - Math.random());
    return shuffled.slice(0, count);
  };

  // useEffect  gi hm trn phim mt ln khi component c mount
  useEffect(() => {
    if (movies.fetchTrending.results.length > 0) {
      setRandomMovies(getRandomMovies(movies.fetchTrending.results, 10));
    }
  }, [movies.fetchTrending.results]); // Mng dependencies rng m bo rng effect ny ch chy mt ln

  // Hm x l khi mt phim c click.
  const handleMovieClick = (type, movie) => {
    // Ly trng thi hin ti ca danh sch phim c click.
    const typeState = movieStates[type];

    // Kim tra nu phim  c mount (hin th) v c cng ID vi phim c click.
    if (typeState.mounted && typeState.data?.id === movie.id) {
      // Nu ng, dispatch action resetState  t li state trong Redux store.
      dispatch(resetState());
      // t li state cc b, ng component hin th thng tin phim.
      setMovieStates((prevState) => ({
        ...prevState,
        [type]: { mounted: false, data: null, key: null },
      }));
    } else {
      // Nu phim khc c click, cp nht state cc b v dispatch action  ly 'Trailerkey'.
      setMovieStates((prevState) => ({
        ...prevState,
        [type]: { mounted: true, data: movie, key: null },
      }));
      dispatch(fetchTrailerKey(movie.id));
    }
  };

  // Component MovieList  hin th danh sch cc b phim
  const MovieList = ({ movies, onMovieClick, type, name }) => {
    // useRef  to mt tham chiu n phn t DOM m bn mun cun
    const sliderRef = useRef();
    const totalWidth = movies.length * 200; // Tng chiu rng ca danh sch phim
    // Khi cun sang tri v mun n nt cun sang tri
    const slideLeft = () => {
      if (sliderRef.current) {
        if (sliderRef.current.scrollLeft === 0) {
          sliderRef.current.scrollLeft = totalWidth;
        } else {
          sliderRef.current.scrollLeft -= 500; // Cun mt phim mi ln
        }
      }
    };

    // Tng t cho hm slideRight nu bn mun thay i opacity cho nt cun sang phi
    const slideRight = () => {
      if (sliderRef.current) {
        if (sliderRef.current.scrollLeft >= totalWidth) {
          sliderRef.current.scrollLeft = 0;
        } else {
          sliderRef.current.scrollLeft += 500; // Cun mt phim mi lncung
        }
      }
    };

    // JSX  render UI ca component
    return (
      <>
        <h1 className="mx-6 mt-[100px] text-xl font-bold text-white pb-3 px-3 ">
          {name}
        </h1>
        <div className="relative flex items-center ">
          {/* Nt cun sang tri */}
          <div
            style={{ opacity: 0.5 }}
            className="cursor-pointer transform hover:scale-125"
          >
            {/* Khi click vo icon ny, hm slideLeft s c gi */}
            <MdChevronLeft onClick={slideLeft} size={40} color="#ffff" />
          </div>
          {/* Container cha danh sch cc poster phim */}
          <div
            id="slider"
            ref={sliderRef}
            className="hidden-scrollbar overflow-x-scroll scrollbar-hide overflow-y-hidden whitespace-nowrap scroll-smooth space-x-2 py-2"
          >
            {/* Map qua mng movies  hin th tng b phim */}
            {movies.map((movie) => (
              <img
                key={movie.id}
                className="w-[15%] h-auto inline-block cursor-pointer transform hover:scale-125 hover:z-20 relative"
                src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                alt={movie.title}
                onClick={() => onMovieClick(type, movie)} //Khi click vo poster, hm onMovieClick s c gi vi tham s type v movie
              />
            ))}
          </div>
          {/* Nt cun sang phi */}
          <div
            style={{ opacity: 0.5 }}
            className="cursor-pointer transform hover:scale-125"
          >
            {/* Khi click vo icon ny, hm slideRight s c gi */}
            <MdChevronRight onClick={slideRight} size={40} color="#ffff" />
          </div>
        </div>
      </>
    );
  };

  // S dng useEffect  cp nht 'Trailerkey' trong state cc b khi n thay i.
  useEffect(() => {
    // Hm cp nht 'Trailerkey' cho danh sch phim tng ng.
    const updateMovieState = (type, key) => {
      setMovieStates((prevState) => ({
        ...prevState,
        [type]: {
          ...prevState[type],
          key,
        },
      }));
    };

    // Khi 'Trailerkey' v 'status' thay i, kim tra v cp nht 'Trailerkey' cho phim  mount.
    if (Trailerkey && status === "succeeded") {
      Object.keys(movieStates).forEach((type) => {
        if (movieStates[type].mounted && !movieStates[type].key) {
          updateMovieState(type, Trailerkey);
        }
      });
    }
  }, [Trailerkey, movieStates, status]);

  return (
    <div className="pb-[150px]">
      <>
        {/* Danh sch cc phim Netflix Originals */}
        <div className="flex justify-center hidden-scrollbar custom-scrollbar overflow-y-hidden space-x-3 py-6 mx-8 my-9 ">
          {randomMovies.map((index) => (
            <img
              key={index.id}
              className="w-[9.3%] h-[auto] transform hover:scale-125 hover:z-20 relative z-10"
              src={`https://image.tmdb.org/t/p/original${index.poster_path}`}
              alt=""
              onClick={() => handleMovieClick("randomTrendingMovies", index)}
            />
          ))}
        </div>

        <div>
          {/* 
          y l ni s hin th thng tin chi tit ca phim khi ngi dng click vo.
          */}
          {movieStates.randomTrendingMovies.mounted && (
            <InformationMove
              movie={movieStates.randomTrendingMovies.data}
              keymovie={movieStates.randomTrendingMovies.key}
            />
          )}
        </div>
        {/* Danh sch phim xp hng xu hng */}
        <MovieList
          movies={movies.fetchTrending.results}
          onMovieClick={handleMovieClick}
          type={"fetchTrending"}
          name={"Xu hng"}
        />

        {/* Hin th thng tin phim xu hng khi ngi dng click vo.*/}
        {movieStates.fetchTrending.mounted && (
          <InformationMove
            movie={movieStates.fetchTrending.data}
            keymovie={movieStates.fetchTrending.key}
          />
        )}

        {/* Danh sch phim xp hng cao */}
        <MovieList
          name={"Xp hng cao"}
          movies={movies.fetchTopRated.results}
          type={"fetchTopRated"}
          onMovieClick={handleMovieClick}
        />

        {/* Hin th thng tin phim xp hng cao khi ngi dng click vo.*/}
        {movieStates.fetchTopRated.mounted && (
          <InformationMove
            movie={movieStates.fetchTopRated.data}
            keymovie={movieStates.fetchTopRated.key}
          />
        )}

        {/* Danh sch phim hnh ng */}
        <MovieList
          name={"Hnh ng"}
          movies={movies.fetchActionMovies.results}
          type={"fetchActionMovies"}
          onMovieClick={handleMovieClick}
        />
        {/* 
        Hin th thng tin phim hnh ng khi ngi dng click vo.
        */}
        {movieStates.fetchActionMovies.mounted && (
          <InformationMove
            movie={movieStates.fetchActionMovies.data}
            keymovie={movieStates.fetchActionMovies.key}
          />
        )}

        {/* Danh sch phim hi */}
        <MovieList
          name={"Hi"}
          movies={movies.fetchComedyMovies.results}
          type={"fetchComedyMovies"}
          onMovieClick={handleMovieClick}
        />
        {/* 
        Hin th thng tin phim hi khi ngi dng click vo.
        */}
        {movieStates.fetchComedyMovies.mounted && (
          <InformationMove
            movie={movieStates.fetchComedyMovies.data}
            keymovie={movieStates.fetchComedyMovies.key}
          />
        )}

        {/* Danh sch phim kinh d */}
        <MovieList
          name={"Kinh d"}
          movies={movies.fetchHorrorMovies.results}
          type={"fetchHorrorMovies"}
          onMovieClick={handleMovieClick}
        />
        {/* 
        Hin th thng tin phim kinh d khi ngi dng click vo.
        */}
        {movieStates.fetchHorrorMovies.mounted && (
          <InformationMove
            movie={movieStates.fetchHorrorMovies.data}
            keymovie={movieStates.fetchHorrorMovies.key}
          />
        )}

        {/* Danh sch phim tnh cm */}
        <MovieList
          name={"Lng mng"}
          movies={movies.fetchRomanceMovies.results}
          type={"fetchRomanceMovies"}
          onMovieClick={handleMovieClick}
        />
        {/* 
        Hin th thng tin phim tnh cm khi ngi dng click vo.
        */}
        {movieStates.fetchRomanceMovies.mounted && (
          <InformationMove
            movie={movieStates.fetchRomanceMovies.data}
            keymovie={movieStates.fetchRomanceMovies.key}
          />
        )}

        {/* Danh sch phim ti liu */}
        <MovieList
          name={"Ti liu"}
          movies={movies.fetchDocumentaries.results}
          type={"fetchDocumentaries"}
          onMovieClick={handleMovieClick}
        />
        {/* 
        Hin th thng tin phim ti liu khi ngi dng click vo.
        */}
        {movieStates.fetchDocumentaries.mounted && (
          <InformationMove
            movie={movieStates.fetchDocumentaries.data}
            keymovie={movieStates.fetchDocumentaries.key}
          />
        )}
      </>
    </div>
  );
};

export default MovieList;
