import React from "react";
import YouTube from "react-youtube";

const InformationMove = ({ movie, keymovie }) => {
  const opts = {
    height: "400",
    width: "100%",
    playerVars: {
      autoplay: 1,
      loop: 1, // Lp li video
      // Ty chn  n thanh iu khin ca video
      controls: 0,
      // Ty chn  n tiu  v thi lng ca video
      showinfo: 0,
      // Ty chn  n logo YouTube trn thanh iu khin
      modestbranding: 1,
      // Ty chn  n cc video lin quan sau khi video kt thc
      rel: 0,
      // Ty chn  n tiu  ca video
      title: 0,
      // Ty chn  n cc nt "Xem sau", "Chia s" v "Thng tin" (tiu  v thi lng) ca video
      iv_load_policy: 3,
    },
  };

  const onEnd = (event) => {
    // Ly tham chiu n player khi video kt thc
    const player = event.target;
    // Tua li video v thi im 0
    player.seekTo(0);
  };

  if (!movie) {
    //|| !keymovie
    return null; // Hoc thc hin mt hnh ng khc ty thuc vo trng hp
  }

  return (
    <div className="bg-[#111] flex px-6 justify-between mt-2">
      <div className="w-[48%] ml-7 mr-4">
        <h1 className="text-white font-bold text-3xl border-b-2 border-white py-10 ">
          {movie.title || movie.original_name}
        </h1>
        <div className=" font-bold text-white text-xl py-5">
          <p className="text-white">
            Release Date: {movie.first_air_date || movie.release_date}{" "}
          </p>
          <p className="text-white">Vote: {movie.vote_average}/10</p>
        </div>
        <p className="text-white">{movie.overview}</p>
      </div>
      <div className="w-[50%]">
        {keymovie ? (
          <div
            style={{ position: "relative", paddingBottom: "56.25%", height: 0 }}
          >
            <YouTube
              className="absolute top-0 left-0 w-full h-full"
              videoId={keymovie}
              opts={opts}
              onEnd={onEnd}
            />
          </div>
        ) : (
          <img
            className="w-full h-full object-cover"
            src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
            alt="Backdrop"
          />
        )}
      </div>
    </div>
  );
};

export default InformationMove;
