import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { API_KEY } from "../custom/apiEndpots";

// Thunk ny c s dng  gi API v ly trailer key da trn ID ca phim.
export const fetchTrailerKey = createAsyncThunk(
  'movies/fetchTrailerKey',
  async (id, { rejectWithValue }) => {
    try {
      // Gi API  ly thng tin video ca phim.
      const response = await fetch(
        `https://api.themoviedb.org/3/movie/${id}/videos?api_key=${API_KEY}`
      );

      // Kim tra xem response c thnh cng khng.
      if (!response.ok) {
        throw new Error('Failed to fetch trailer data');
      }

      // Parse response data thnh JSON.
      const data = await response.json();
    
      // Lc kt qu  ch ly trailers hoc teasers t YouTube.
      const filteredResults = data.results
        .filter(result => result.type === 'Trailer' || result.type === 'Teaser')
        .filter(result => result.site === 'YouTube');

      // Nu tm thy trailer, tr v key ca trailer u tin.
      if (filteredResults.length > 0) {
        return filteredResults[0].key;
      } else {
        throw new Error('No trailer found');
      }
    } catch (error) {
      // Nu c li, tr v li thng qua rejectWithValue.
      return rejectWithValue(error.message);
    }
  }
);

// To mt slice vi tn 'movies', state ban u v reducers.
const movieSlice = createSlice({
  name: 'movies',
  initialState: {
    key: null, // Key ca trailer, ban u l null.
    error: null, // Lu tr thng bo li, ban u l null.
    status: 'idle' // Trng thi ca slice, ban u l 'idle'.
  },
  reducers: {
    // Reducer  reset state v gi tr ban u.
    resetState: (state) =>{
      state.key = null;
      state.error = null;
      state.status = 'idle';
    }
  },
  extraReducers: (builder) => {
    // X l cc trng thi ca async thunk.
    builder
      .addCase(fetchTrailerKey.pending, (state) => {
        // Khi bt u gi API, t trng thi l 'loading'.
        state.status = 'loading';
      })
      .addCase(fetchTrailerKey.fulfilled, (state, action) => {
        // Khi gi API thnh cng, cp nht state vi key mi v t trng thi l 'succeeded'.
        state.status = 'succeeded';
        state.key = action.payload;
        state.error = null;
      })
      .addCase(fetchTrailerKey.rejected, (state, action) => {
        // Khi gi API tht bi, cp nht state vi li v t trng thi l 'failed'.
        state.status = 'failed';
        state.error = action.payload;
      });
  }
});

// Export action resetState  c th s dng n trong cc components.
export const {resetState} = movieSlice.actions;

// Export reducer  s dng trong store ca Redux.
export default movieSlice.reducer;
