// Import cc hooks v components t React v react-router-dom
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

// Component Navbar  hin th thanh iu hng
const Navbar = () => {
  // State  theo di trng thi cun ca trang
  const [isScrolled, setIsScrolled] = useState(false);

  // useEffect  thit lp v dn dp event listener cho s kin cun trang
  useEffect(() => {
    // Hm x l s kin cun trang
    const handleScroll = () => {
      // Nu v tr cun ln hn 100px, t isScrolled thnh true
      if (window.scrollY > 100) {
        setIsScrolled(true);
      } else {
        // Ngc li, t isScrolled thnh false
        setIsScrolled(false);
      }
    };

    // Thm event listener cho s kin cun trang
    window.addEventListener("scroll", handleScroll);

    // Dn dp event listener khi component b unmount
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []); // Mng dependencies rng m bo rng effect ny ch chy mt ln

  // Hm x l s kin khi nhn vo tiu  "Movies app"
  const handleScrollToTop = () => {
    // Cun ln u trang vi hiu ng smooth
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  // JSX  render UI ca Navbar
  return (
    <div
      // S dng template literals v conditional rendering  thay i class ca div
      className={`flex justify-between px-6 py-6 text-red-500 fixed top-0 left-0 w-full z-40 ${
        isScrolled ? "bg-black" : ""
      } transition-colors duration-500`}
    >
      {/* Link n trang ch */}
      <Link to="/">
        {/* Tiu  "Movies app" c s kin onClick  cun ln u trang */}
        <h1 className="font-bold text-2xl" onClick={handleScrollToTop}>Movies app</h1>
      </Link>
      {/* Link n trang tm kim */}
      <Link to="/search">
        {/* Icon tm kim vi s kin onClick  cun ln u trang */}
        <svg
          className="svg-inline--fa fa-search fa-w-16 h-6 transform transition-transform duration-300 hover:scale-125 "
          onClick={handleScrollToTop}
          fill="#ccc"
          aria-hidden="true"
          data-prefix="fas"
          data-icon="search"
          role="img"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 512 512"
        >
          {/* ng dn SVG cho icon tm kim */}
          <path d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"></path>
        </svg>
      </Link>
    </div>
  );
};

// Xut khu component Navbar  s dng  ni khc
export default Navbar;
