import React, { useEffect, useState } from "react";
import { useData } from "../../../custom/GlobalData"; // Import GlobalData t useContext

const Banner = () => {
  // State  lu tr mt phim ngu nhin
  const [randomMovie, setRandomMovie] = useState(null); 
  // Ly d liu phim t useContext
  const { movies } = useData();

  // In ra danh sch phim hnh ng  kim tra d liu
  //console.log(movies.fetchActionMovies.results);

  // S dng useEffect  ly mt phim ngu nhin t mng movies khi movies thay i
  useEffect(() => {
    if (movies.fetchTrending.results.length > 0) {
      // Ly mt ch s ngu nhin trong mng fetchTrending
      const randomIndex = Math.floor(Math.random() * movies.fetchTrending.results.length);
      // Ly phim ngu nhin t mng fetchTrending v lu vo state
      setRandomMovie(movies.fetchTrending.results[randomIndex]);
    }
  }, [movies]);

  // In ra phim ngu nhin  kim tra
  
  return (
    <div className="relative h-[1000px]">
      {randomMovie && (
        <div
          className="absolute top-0 left-0 w-full h-full"
          style={{
            backgroundImage: `url('https://image.tmdb.org/t/p/original${randomMovie.backdrop_path}')`,
            backgroundSize: "cover",
            backgroundPosition: "center",
            
          }}
        >
          {/* Cc phn t khc nm trn background */}
          <div className="relative z-10 top-[25%] left-[2%] w-[350px]">
            <h1 className="text-5xl text-white font-bold">
              {randomMovie.original_title || randomMovie.original_name}
            </h1>
            <div className="flex">
              <button className="bg-[#52525b] opacity-70 mt-8 px-4 py-1 text-white text-xl mr-3">
                Play
              </button>
              <button className="bg-[#52525b] opacity-70 mt-8 px-4 py-1 text-white text-xl mr-3">
                My List
              </button>
            </div>
            <p className="text-white mt-2">{randomMovie.overview}</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default Banner;
